import QRCode from 'qrcodejs2';
export default {
    name: "award",
    data(){
        return{
            toUrl: 'https://demo.isaveu.cn/mobile/#/userEvaluation',
            shop_id: null,
            id: null,
        }
    },
    mounted(){
        this.id = this.$route.params.id;
        this.shop_id = this.$route.params.shop_id;
        this.toUrl = this.toUrl + '?shopId=' + this.shop_id + '&id=' + this.id;
        this.qrcode();
    },
    methods:{
        //   返回
        blck(){
            this.$router.push('/home')
        },
        // 生成二维码
        qrcode(){
            let qrcode = new QRCode('qrcode',{
                width: 200, // 设置宽度，单位像素
                height: 200, // 设置高度，单位像素
                text: this.toUrl,   // 设置二维码内容或跳转地址
            })
        },
    }
}



